@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap");
body {
  font-family: "Poppins", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
.main-pricing h1 {
  justify-items: center;
  align-items: center;
  text-align: center;
}
.main-pricing h3 {
  justify-items: center;
  align-items: center;
  text-align: center;
  color: #1bbcfd;
}
nav {
  width: 100vw;
  height: 10vh;
  background-color: white;
  position: fixed;
  top: 0;
}
.container {
  padding-right: 10vw;
  padding-left: 10vw;
}
.img-bot {
  width: 100vw;
}
.top-img {
  width: 100%;
  margin-top: -30vh;
}
.img-home {
  float: right;
  width: 50%;
  margin-top: -25vh;
}

.left-nav {
  float: left;
  top: 0;
  padding: 1vw 2vw;
}
.right-nav {
  float: right;
}
.right-nav ul {
  list-style-type: none;
}
.right-nav li {
  float: left;
  font-size: 1.2vw;
  top: 0;
  padding: 2vw 2vw;
}
.right-nav li {
  color: black;
}
.right-nav li:hover {
  color: grey;
}
.main-fitur {
  width: 100vw;
  height: 100vh;
  margin-bottom: 20vh;
}
.logo-nav {
  width: 15vw;
}
.main-promo {
  margin-top: 5vh;
}
.main-contact h3 {
  justify-items: center;
  align-items: center;
  text-align: center;
  color: #1bbcfd;
}
.main-contact h1 {
  justify-items: center;
  align-items: center;
  text-align: center;
  margin-bottom: 10vh;
}
.contact-content {
  max-width: 50%;
}
.img-kontak {
  width: 40%;
  margin-top: -45vh;
  margin-left: 60vw;
}
footer {
  width: 100vw;
  color: white;
  background-color: #202020;
}
.contact-contect p {
  margin-bottom: 5vh;
}

.card-promo {
  border: none;
  width: 18vw;
  height: 45vh;
  margin: 2vw;
  padding-top: 1vw;
  padding-left: 2vw;
  padding-right: 2vw;
  box-shadow: 0.3vw 0.3vw 0.5vw grey;
  float: left;
  border-radius: 0.5vw;
}
.fitur-pricing p {
  margin-bottom: 1vh;
}
.card-pricing {
  border: none;
  width: 18vw;
  height: 75vh;
  margin: 2vw;
  padding-top: 1vw;
  padding-left: 2vw;
  padding-right: 2vw;
  box-shadow: 0.3vw 0.3vw 0.5vw grey;
  float: left;
  border-radius: 0.5vw;
}
.btn-promo {
  width: 18vw;
  height: 5vh;
  color: white;
  background-color: black;
  padding: 0.5vw 0.5vw;
  border: none;
  border-radius: 0.5vw;
  font-weight: bolder;
}
.btn-pricing {
  width: 18vw;
  height: 5vh;
  color: white;
  background-color: black;
  padding: 0.5vw 0.5vw;
  border: none;
  border-radius: 0.5vw;
  font-weight: bolder;
}
.card-promo img {
  width: 18vw;
  border-radius: 0.5vw;
  margin-bottom: 1vh;
}
.card-pricing img {
  width: 18vw;
  border-radius: 0.5vw;
  margin-bottom: 1vh;
}
.card-promo h4 {
  font-size: 1.5vw;
  color: #888888;
}
.card-pricing h4 {
  font-size: 1.5vw;
  color: #888888;
}
.card-promo span {
  font-size: 1vw;
  background-color: red;
  color: white;
  float: right;
  padding: 0.5vw 0.5vw;
  border-radius: 0.5vw;
  margin-top: -3vw;
  font-weight: bolder;
}
.card-pricing span {
  font-size: 1vw;
  background-color: red;
  color: white;
  float: right;
  padding: 0.5vw 0.5vw;
  border-radius: 0.5vw;
  margin-top: -3vw;
  font-weight: bolder;
}
.card-img img {
  width: 100%;
}
.card-promo p {
  font-size: 1vw;
  color: #888888;
  max-width: 70%;
}
.card-pricing p {
  font-size: 1vw;
  color: #888888;
  max-width: 70%;
}
.card-promo-title {
  padding-bottom: 1vw;
}
.card-pricing h3 {
  border-bottom: solid 0.2vh grey;
  padding-bottom: 2vh;
  padding-top: 2vh;
  color: #888888;
  margin-bottom: 1vh;
}
.card-pricing-title {
  padding-bottom: 1vw;
  border-bottom: solid 0.2vh grey;
}
.main-promo h1 {
  justify-items: center;
  align-items: center;
  text-align: center;
}
.main-promo h3 {
  color: #1bbcfd;
  justify-items: center;
  align-items: center;
  text-align: center;
}
.main-fitur h1 {
  justify-items: center;
  align-items: center;
  text-align: center;
  margin-bottom: 50px;
}
.reak-card {
  float: right;
  width: 5vw;
  margin-right: -4vw;
  margin-top: -7vh;
}
.main-fitur h3 {
  justify-items: center;
  align-items: center;
  text-align: center;
  color: #1bbcfd;
}
.card {
  border: none;
  width: 18vw;
  height: 25vh;
  margin: 2vw;
  padding-top: 1vw;
  padding-left: 2vw;
  padding-right: 2vw;
  box-shadow: 0.3vw 0.3vw 0.5vw grey;
  float: left;
  border-radius: 0.5vw;
}
.card h2 {
  font-size: 1.8vw;
  color: #888888;
}
.card p {
  font-size: 0.9vw;
  color: grey;
}
.card-img {
  float: left;
  width: 5vw;
  height: 100%;
  margin-right: 1vw;
}
.main-home {
  height: 100vh;
  width: 100vw;
}
.main-content {
  padding: 0 25px 0 25px;
  margin-top: 25vh;
  max-width: 50%;
}
.main-content h1 {
  font-size: 4vw;
  color: gray;
}
.main-content p {
  font-size: 1.5vw;
  margin-top: 25px;
  color: grey;
}
.main-content button {
  background-color: rgb(34, 34, 34);
  color: white;
  margin-top: 10vh;
  border: none;
  font-weight: 900;
  border-radius: 15px;
  font-size: 1vw;
  margin-right: 25px;
  padding: 1vw 2.5vw;
}
.main-content button:hover {
  background-color: gray;
}

@media only screen and (max-width: 800px) {
  .fitur-pricing p {
    margin-bottom: 5px;
  }
  .right-nav {
    display: none;
  }
  .promo-main h1 {
    font-size: 100px;
  }
  .card {
    width: 250px;
    height: 150px;
    margin: 20px;
    padding-top: 25px;
    padding-left: 25px;
    padding-right: 25px;
    box-shadow: 3px 3px 5px grey;
    float: left;
    border-radius: 5px;
  }
  .left-nav {
    padding: 10px 25px;
  }
  nav {
    width: 100%;
    height: 60px;
  }
  .reak-card {
    float: right;
    width: 50px;
    margin-right: -40px;
    margin-top: -45px;
  }
  .main-content button {
    margin-right: 5px;
  }
  .main-content {
    max-width: 100%;
    height: 700px;
    margin-top: 15vh;
  }
  .main-content h1 {
    font-size: 1.5cm;
  }
  .main-content p {
    font-size: 0.4cm;
  }
  .main-content button {
    padding: 10px 25px;
    font-size: 0.3cm;
  }

  .logo-nav {
    width: 150px;
  }
  .card-group {
    justify-items: center;
    align-items: center;
    margin-left: 10px;
  }
  .container {
    padding-right: 1vw;
    padding-left: 1vw;
  }
  .img-home {
    display: none;
  }
  .img-bot {
    display: none;
  }
  .top-img {
    display: none;
  }
  .card-img {
    float: left;
    width: 20%;
    height: 100%;
    margin-right: 10px;
  }
  .card h2 {
    font-size: 20px;
  }
  .card p {
    font-size: 15px;
  }
  .card-promo {
    width: 250px;
    height: 300px;
    margin: 20px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: 3px 3px 5px grey;
    border-radius: 5px;
  }
  .card-promo img {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .card-promo h4 {
    font-size: 15px;
  }
  .card-promo span {
    font-size: 10px;
    padding: 5px 5px;
    border-radius: 5px;
    margin-top: -50px;
  }
  .btn-promo {
    width: 100%;
    height: 35px;
    padding: 5px 5px;
    border-radius: 5px;
  }
  .card-promo p {
    font-size: 10px;
    max-width: 70%;
    margin-bottom: 20px;
  }
  .main-fitur {
    margin-bottom: 800px;
  }
  .main-promo {
    margin-bottom: 1150px;
  }
  .main-pricing {
    margin-bottom: 1750px;
  }
  .card-pricing {
    width: 250px;
    height: 500px;
    margin: 20px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: 3px 3px 5px grey;
    border-radius: 5px;
  }
  .card-pricing img {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .card-pricing h4 {
    font-size: 15px;
  }
  .card-pricing span {
    font-size: 10px;
    padding: 5px 5px;
    border-radius: 5px;
    margin-top: -50px;
  }
  .btn-pricing {
    width: 100%;
    height: 35px;
    padding: 5px 5px;
    border-radius: 5px;
  }
  .card-pricing p {
    font-size: 10px;
    max-width: 70%;
    margin-bottom: 20px;
  }
  .main-contact h1 {
    margin-bottom: 50px;
  }
  .contact-content {
    max-width: 80%;
  }
  .main-contact {
    margin-top: 600px;
  }
  .img-kontak {
    display: none;
    margin: 0;
  }
}
